@font-face {
    font-family: 'LogoFont';
    src: local('LogoFont'), url(../font/Logo.otf) format('opentype');
  }
.Field{
    width: 100%;
    height: 110px;
    position: fixed;
    margin-bottom: 80px;
    background-color: rgba(255, 255, 255, 0.87);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.Logo {
    font-family: "LogoFont";
    font-size: 80px;
    font-weight: 700;
    color: rgb(121, 185, 187);
    margin-left: 10px;
}

.Menu{
    display: flex;
    justify-content: space-around;
    width: 50%;
}