.Box{
    width: 320px;
    display: block;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.699);
    margin: 8px;
    border-radius: 10px;
    box-shadow: 0px 0px 15px 1px #c0c0c0;
    border: 2px solid rgb(204, 200, 200);
}
.Image {
    width: 100%;
    border-radius: 20px;
    margin-bottom: 15px;
}

.Image img{
    width: 100%;
    height: 200px;
    border-radius: 10px; 
}
