@font-face {
    font-family: 'LogoFont';
    src: local('LogoFont'), url(../font/Logo.otf) format('opentype');
  }
.Field {
    position: relative;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(90deg, rgba(186, 212, 240, 1), rgb(178, 207, 238) 21%, rgba(233, 240, 250, 1));
}

.Logo {
    text-align: center;
    font-family: "LogoFont";
    color: #7a7a7a;
}

.Form {
    position: absolute;
    left: 50%;
    top: 50%;
    opacity: 0.9;
    box-shadow: 0px 0px 15px 1px #c0c0c0;
    border-radius: 10px;
    transform: translate(-50%, -50%);
    width: 20%;
    height: 500px;
    background-color: white;
    border: 2px solid rgb(204, 200, 200);
}

.Input {
    width: 80%;
    height: 30px;
    margin-left: 8%;
    margin-top: 10%;
    outline: none;
}

.Button {
    width: 80%;
    height: 30px;
    margin-left: 9%;
    margin-top: 10%;
}